<template>
  <div>
    <div class="search">
      <el-input placeholder="" style="width: 200px" v-model="title"></el-input>
      <el-button type="info" plain style="margin-left: 10px" @click="load(1)"></el-button>
      <el-button type="warning" plain style="margin-left: 10px" @click="reset"></el-button>
    </div>

    <div class="operation">
      <el-button type="primary" plain @click="handleAdd"></el-button>
      <el-button type="danger" plain @click="delBatch"></el-button>
    </div>

    <div class="table">
      <el-table :data="tableData" strip @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center"></el-table-column>
        <el-table-column prop="id" label="" width="70" align="center" sortable></el-table-column>
        <el-table-column prop="title" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="descr" label="" show-overflow-tooltip></el-table-column>
        <el-table-column label="">
          <template v-slot="scope">
            <el-button @click="preview(scope.row.content)"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="cover" label="">
          <template v-slot="scope">
            <el-image :preview-src-list="[scope.row.cover]" :src="scope.row.cover" style="width: 80px; height: 90px;"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="time" label=""></el-table-column>
        <el-table-column prop="count" label=""></el-table-column>
        <el-table-column prop="category" label=""></el-table-column>
        <el-table-column label="" align="center" width="180">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)"></el-button>
            <el-button size="mini" type="danger" plain @click="del(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[5, 10, 20]"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>
    </div>


    <el-dialog title="" :visible.sync="fromVisible" width="50%" :close-on-click-modal="false" destroy-on-close>
      <el-form :model="form" label-width="100px" style="padding-right: 50px" :rules="rules" ref="formRef">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="descr">
          <el-input type="textarea" v-model="form.descr" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="cover">
          <el-upload
              class="avatar-uploader"
              :action="$baseUrl + '/files/upload'"
              :headers="{ token: user.token }"
              list-type="picture"
              :on-success="handleCoverSuccess"
          >
            <el-button type="primary"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="category">
          <el-select style="width: 100%" v-model="form.category">
            <el-option v-for="item in categoryList" :key="item.id" :value="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="content">
          <div id="editor"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fromVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="fromVisible1" width="50%" :close-on-click-modal="false" destroy-on-close>
      <div class="w-e-text">
        <div v-html="content"></div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="fromVisible1 = false"> </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import E from "wangeditor"

export default {
  name: "News",
  data() {
    return {
      tableData: [],  // 
      pageNum: 1,   // 
      pageSize: 10,  // 
      total: 0,
      title: null,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('xm-user') || '{}'),
      rules: {
        title: [
          {required: true, message: '', trigger: 'blur'},
        ],
        descr: [
          {required: true, message: '', trigger: 'blur'},
        ]
      },
      ids: [],
      categoryList: [],
      content: '',
      fromVisible1: false
    }
  },
  created() {
    this.load(1)

    this.$request.get('/category/selectAll').then(res => {
      this.categoryList = res.data || []
    })
  },
  methods: {
    handleAdd() {   // 
      this.form = {}  // 
      this.fromVisible = true   // 
      this.setRichText('')
    },
    handleEdit(row) {   // 
      this.form = JSON.parse(JSON.stringify(row))  // form  
      this.fromVisible = true   // 
      this.setRichText(this.form.content)
    },
    save() {   //   
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.form.content = this.editor.txt.html()
          this.$request({
            url: this.form.id ? '/news/update' : '/news/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 
              this.$message.success('')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    del(id) {   // 
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/news/delete/' + id).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    handleSelectionChange(rows) {   // 
      this.ids = rows.map(v => v.id)   //  [1,2]
    },
    delBatch() {   // 
      if (!this.ids.length) {
        this.$message.warning('')
        return
      }
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/news/delete/batch', {data: this.ids}).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {
      })
    },
    load(pageNum) {  // 
      if (pageNum) this.pageNum = pageNum
      this.$request.get('/news/selectPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          title: this.title,
        }
      }).then(res => {
        this.tableData = res.data?.list
        this.total = res.data?.total
      })
    },
    reset() {
      this.title = null
      this.load(1)
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
    handleCoverSuccess(res) {
      this.form.cover = res.data
    },
    setRichText(html) {
      this.$nextTick(() => {
        this.editor = new E(`#editor`)
        this.editor.config.uploadImgServer = this.$baseUrl + '/files/editor/upload'
        this.editor.config.uploadFileName = 'file'
        this.editor.config.uploadImgHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadImgParams = {
          type: 'img',
        }
        this.editor.create()  // 
        this.editor.txt.html(html)
      })
    },
    preview(content) {
      this.content = content
      this.fromVisible1 = true
    },
  }
}
</script>

<style scoped>

</style>
